<template>
  <div
    class="container d-flex justify-center align-center"
    :class="{ 'load-card-grey': disabled }"
    :style="{
      backgroundColor: color,
    }"
  >
    {{ text }}
  </div>
</template>
<script>
import stc from 'string-to-color';

export default {
  name: 'LetterAvatar',
  props: {
    name: {
      type: String,
      required: true,
    },
    disabled: {
      type: Boolean,
      default: false,
    },
  },
  computed: {
    text() {
      const worlds = this.name.split(' ');
      return worlds.reduce((acc, item) => acc + item[0], '');
    },
    color() {
      return stc(this.text);
    },
  },
};
</script>

<style scoped lang="scss">
.container {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  font-weight: 600;
  font-size: 14px;
  color: white;
}
</style>
