<template>
  <base-layout
    :is-header-showed="!isHeaderHidden"
    :white-background="$vuetify.breakpoint.mobile"
    :loading="drivers.loading"
  >
    <template slot="header">
      <div class="d-flex justify-space-between align-center full-width">
        <div class="d-flex">
          <v-btn
            v-if="isBackButtonVisible"
            text
            depressed
            rounded
            color="transparent"
            min-width="auto"
            height="auto"
            class="px-0"
            :to="{ name: $constants.ROUTE_NAMES.DRIVER }"
          >
            <v-img
              class="mr-4"
              alt="Back"
              :max-width="18"
              :src="require('@images/icons/drivers/back.svg')"
            />
          </v-btn>

          <h1
            v-if="!$vuetify.breakpoint.mobile"
            class="font-weight-bold primary-dark--text"
          >
            {{ $route.meta.pageTitle }}
          </h1>
        </div>

        <div
          v-if="isHeaderControlsVisible && $vuetify.breakpoint.mobile && drivers.data.length"
          class="flex-shrink-0 flex-grow-1 search-bar mr-2"
        >
          <v-autocomplete
            :search-input.sync="searchDriver"
            class="font-weight-medium body-2 text-label"
            outlined
            dense
            hide-details
            hide-no-data
            clearable
            label="Search for drivers"
            append-icon="mdi-magnify"
          />
        </div>

        <v-btn
          :to="{ name: $constants.ROUTE_NAMES.DRIVER_INVITE }"
          v-if="isHeaderControlsVisible && isCanManageDriver"
          default
          depressed
          class="flex-shrink-1 flex-grow-0 primary-light primary--text font-weight-medium custom__btn"
          :max-width="$vuetify.breakpoint.mobile ? 50 : 'auto'"
          width="auto"
          :height="$vuetify.breakpoint.mobile ? 50 : 35"
        >
          <v-img
            v-if="$vuetify.breakpoint.mobile"
            alt="plus"
            :max-width="18"
            :src="require('@images/icons/drivers/plus.svg')"
          />
          <div
            v-else
            class="d-flex"
          >
            <span>
              Invite driver
            </span>
            <v-img
              alt="add"
              :max-width="18"
              class="ml-3"
              :src="require('@images/icons/contacts/add-user.svg')"
            />
          </div>
        </v-btn>
      </div>
    </template>

    <template slot="content">
      <base-no-list-message v-if="!drivers.data.length">
        You don't have any drivers yet
      </base-no-list-message>
      <div
        v-else
        class="content-page-wrapper d-flex flex-column flex-md-row flex-shrink-1 px-lg-6 py-lg-8 px-0 my-md-0 mx-lg-n2 mx-md-auto"
      >
        <slot></slot>
      </div>
    </template>
  </base-layout>
</template>

<script>
import BaseLayout from '@components/BaseLayout';
import BaseNoListMessage from '@components/BaseNoListMessage';
import EventBus from '@utils/eventBus';
import ROLES from '@utils/constants/roles';

import { createNamespacedHelpers, mapState } from 'vuex';

const { mapState: mapDriversState } = createNamespacedHelpers('drivers');

export default {
  name: 'DriversPageWrapper',
  components: {
    BaseLayout,
    BaseNoListMessage,
  },
  data: () => ({
    searchDriver: null,
  }),
  computed: {
    isCanManageDriver() {
      return this.userRole === ROLES.CARRIER;
    },
    isBackButtonVisible() {
      return this.$route.name === this.$constants.ROUTE_NAMES.DRIVER_EDIT && !this.$vuetify.breakpoint.mobile;
    },
    isHeaderHidden() {
      return (
        (this.$route.name === this.$constants.ROUTE_NAMES.DRIVER && this.$vuetify.breakpoint.mobile)
        || (this.$route.name === this.$constants.ROUTE_NAMES.DRIVER_EDIT && this.$vuetify.breakpoint.mobile)
      );
    },
    isHeaderControlsVisible() {
      return (
        (this.$route.name === this.$constants.ROUTE_NAMES.DRIVERS)
        || (this.$route.name === this.$constants.ROUTE_NAMES.DRIVER && !this.$vuetify.breakpoint.mobile)
      );
    },
    ...mapState(['userRole']),
    ...mapDriversState(['drivers']),
  },
  watch: {
    searchDriver(search) {
      EventBus.$emit(this.$constants.EVENTS.SEARCH_EVENT, search);
    },
  },
};
</script>

<style lang="scss" scoped>
::v-deep {
  .v-input .v-label {
    line-height: 14px;
    font-size: 14px;
  }

  .v-select.v-select--is-menu-active .v-input__icon--append .v-icon {
    transform: rotate(0);
  }

  .v-text-field--outlined.v-input--dense.v-text-field--outlined > .v-input__control > .v-input__slot {
    min-height: 35px;
    padding-left: 20px;

    @media screen and (max-width: 1264px) {
      min-height: 50px;
      padding: 7px 13px 7px 20px;
    }
  }

  .v-text-field--enclosed.v-input--dense:not(.v-text-field--solo).v-text-field--outlined .v-input__append-inner {
    margin-top: 6px;
  }

  .v-btn:not(.v-btn--round).v-size--default {
    min-width: 50px;
  }
}

.search-bar {
  max-width: 442px;
  width: 100%;
  height: 35px;

  @media screen and (max-width: 1264px) {
    width: auto;
    height: 50px;
  }
}
</style>
