<template>
  <drivers-page-wrapper>
    <drivers-list
      :is-visible="isVisibleList"
      :drivers-list="drivers.data"
    />
    <router-view />
  </drivers-page-wrapper>
</template>

<script>
import DriversPageWrapper from '@modules/drivers/components/DriversPageWrapper';
import DriversList from '@modules/drivers/components/DriversList';

import * as constants from '@modules/drivers/constants';
import { identity } from 'ramda';
import EventBus from '@utils/eventBus';

import { createNamespacedHelpers } from 'vuex';

const {
  mapActions,
  mapState,
  mapGetters,
  mapMutations,
} = createNamespacedHelpers('drivers');

export default {
  name: 'Drivers',
  components: {
    DriversPageWrapper,
    DriversList,
  },
  data: () => ({
    routeId: null,
    isEditRoute: false,
  }),
  computed: {
    isVisibleList() {
      return (
        this.$route.name === this.$constants.ROUTE_NAMES.DRIVERS
        || (this.$route.name === this.$constants.ROUTE_NAMES.DRIVER && !this.$vuetify.breakpoint.mobile)
      );
    },
    ...mapState(['drivers']),
    ...mapGetters({
      activeDriverId: constants.getters.ACTIVE_DRIVER_ID,
    }),
  },
  watch: {
    '$route.params.id': {
      immediate: true,
      handler(nextId) {
        this.routeId = nextId;
      },
    },
    '$route.fullPath': {
      immediate: true,
      handler(path) {
        this.isEditRoute = /edit/.test(path);
      },
    },
  },
  created() {
    this.getDrivers()
      .catch(() => {
        this.$notificationError('Sorry, we could not get drivers. Please try again or contact administrator.');
      });
  },
  mounted() {
    EventBus.$on(this.$constants.EVENTS.REDIRECT_EVENT, this.redirectToExistDriver);
  },
  beforeUpdate() {
    if (!this.isEditRoute) {
      let path = {
        name: this.$constants.ROUTE_NAMES.DRIVER,
        params: { id: this.activeDriverId },
      };
      if (!this.routeId && !this.$vuetify.breakpoint.mobile) {
        if (!this.activeDriverId) {
          path = {
            name: this.$constants.ROUTE_NAMES.DRIVERS,
          };
        }
        this.$router.push(path).catch(identity);
      } else {
        this.$router.push(
          {
            name: this.$constants.ROUTE_NAMES.DRIVER,
            params: { id: this.routeId },
          },
        ).catch(identity);
        this.updateActiveDriverId(this.routeId);
      }
    }

    if (!this.routeId && this.$vuetify.breakpoint.mobile) {
      this.$router.push(
        {
          name: this.$constants.ROUTE_NAMES.DRIVERS,
          params: { id: this.activeDriverId },
        },
      ).catch(identity);
    }
  },
  methods: {
    ...mapActions({
      getDrivers: constants.actions.GET_DRIVERS,
    }),
    ...mapMutations({
      updateActiveDriverId: constants.mutations.UPDATE_ACTIVE_DRIVER_ID,
    }),
    redirectToExistDriver() {
      this.$router.push(
        {
          name: this.$constants.ROUTE_NAMES.DRIVER,
          params: { id: this.drivers.data[0].id },
        },
      ).catch(identity);
    },
  },
};
</script>
