<template>
  <v-card
    v-if="isVisible"
    :max-width="$vuetify.breakpoint.mobile ? '100%' : 330"
    width="100%"
    class="drivers-list overflow-y-auto mx-lg-2 mb-lg-6"
  >
    <v-toolbar
      v-if="!$vuetify.breakpoint.mobile"
      class="toolbar"
      flat
    >
      <v-autocomplete
        :search-input.sync="searchDriver"
        class="font-weight-medium body-2 text-label"
        outlined
        dense
        hide-details
        hide-no-data
        clearable
        label="Search for driver"
        append-icon="mdi-magnify"
      />
    </v-toolbar>

    <v-list>
      <template v-for="({ full_name, id, status }, index) in filteredDrivers">
        <v-list-item
          class="list-item"
          :class="{ 'inactive': status !== 'Active' }"
          :key="`driver-${id}`"
          :to="{ name: $constants.ROUTE_NAMES.DRIVER, params: { id } }"
        >
          <v-list-item-avatar>
            <letter-avatar
              :name="full_name"
              :disabled="status !== 'Active'"
            />
          </v-list-item-avatar>
          <v-list-item-title
            class="text-left body-2 px-0 label--text font-weight-medium"
          >
            {{ full_name }}
          </v-list-item-title>
          <v-img
            class="time"
            v-if="status === 'Invited'"
            alt="Time"
            :max-width="20"
            :src="require('@images/icons/drivers/time.svg')"
          />
        </v-list-item>
        <v-divider
          v-if="index !== filteredDrivers.length - 1"
          :key="index"
          class="mx-5"
        />
      </template>
    </v-list>
  </v-card>
</template>

<script>
import LetterAvatar from '@components/LetterAvatar';
import EventBus from '@utils/eventBus';

export default {
  name: 'DriversList',
  components: { LetterAvatar },
  props: {
    driversList: {
      type: [Array, Object],
      required: true,
    },
    isVisible: {
      type: Boolean,
      default: true,
    },
  },
  data: () => ({
    searchDriver: null,
  }),
  computed: {
    filteredDrivers() {
      return this.driversList.filter((driver) => {
        if (this.searchDriver) {
          return driver.full_name.toLowerCase().indexOf(this.searchDriver.toLowerCase()) > -1;
        }
        return this.driversList;
      });
    },
  },
  mounted() {
    EventBus.$on(this.$constants.EVENTS.SEARCH_EVENT, this.changeSearchValue);
  },
  methods: {
    changeSearchValue(search) {
      this.searchDriver = search;
    },
  },
};
</script>

<style lang="scss" scoped>
.drivers-list {
  max-height: calc(100vh - 170px);
  height: fit-content;

  @media screen and (max-width: 1264px) {
    max-height: 100%;
    box-shadow: none !important;
  }
}

.v-list-item--active {
  background-color: $list-active-background;
  color: $list-active-background;

  .v-list-item__title {
    color: $list-active-text !important;
  }
}

.theme--light.v-list-item:not(.v-list-item--active):not(.v-list-item--disabled).inactive {

  color: $secondary !important;
}

.toolbar {
  position: sticky;
  top: 0;
  z-index: 1;
}
</style>
